import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import AddBankDetail from "../AddBankDetail";
import {
  useFinanceDeleteBank,
  useFinanceUpdateBank,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../Income/Income.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
const OneBankMenu = ({ onToggle, setBankEdit, openBankMenu }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    query: "",
  });
  const [isDelete, setIsDelete] = useState(false);

  const [financeDeleteBank] = useFinanceDeleteBank();
  const [financeUpdateBank] = useFinanceUpdateBank();

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onEdit = () => {
    setBankEdit((pre) => !pre);
    setEdit((pre) => !pre);
  };
  const onDelete = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      if (state.query === "Confirm" && openBankMenu?._id) {
        setIsDelete(false);
        setDisabled((pre) => !pre);
        financeDeleteBank(openBankMenu?._id)
          .then(() => {
            onToggle();
            setDisabled((pre) => !pre);
            navigate(-1);
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };
  const onUpdateData = (val) => {
    if (openBankMenu?._id) {
      setDisabled((pre) => !pre);
      setEdit(false);
      financeUpdateBank({
        bid: openBankMenu?._id,
        bankDetails: val,
        // delete_pic: prev,
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onToggle();
          setBankEdit((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {isDelete ? (
        <Modal onClose={() => setIsDelete(false)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("are_your_sure_to_delete_bank_account")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setIsDelete(false)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("type_confirm_to_continue")}
                placeholder={t("type_confirm_to_continue")}
                name="query"
                value={state.query}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["query"]}
              />
              <CreateButton label="confirm" onAction={onDelete} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : edit ? (
        <AddBankDetail
          onClose={onEdit}
          openAs="EDIT"
          onEditAction={onUpdateData}
          openBankMenu={openBankMenu}
        />
      ) : (
        <ModalMenu onClose={onToggle} disabled={disabled}>
          <h6 onClick={onEdit}>{t("edit")}</h6>
          <h6 onClick={() => setIsDelete(true)}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default OneBankMenu;
